import {React, useState} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import { useParams, useNavigate, Link, Navigate} from 'react-router-dom';
import DOMPurify from 'dompurify';
import {reviewLoaderSelector, reviewStatusSelector, sendUserReview} from '../../app/slices/reviewsSlice';
import {whichMarket} from '../../apis/functions';
import './reviewPage.css';
/* IMG */

export default function Review({newReview, setNewReview}){
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const loading = useSelector(reviewLoaderSelector);
    const status = useSelector(reviewStatusSelector);
    /* SET UP FORMS */
    const [main, setMain] = useState('');
    const [comment, setComment] = useState('');
    const [rating, setRating] = useState(0);
    const [error, setError] = useState('');

    const handleSubmit = () => {
        if (!main) {
            setError('Favor De Incluir Asunto');
            return;
          }

        if (!comment) {
            setError('Favor De Incluir Un Comentario');
            return;
          }

        if (!rating || rating < 1 || rating > 5) {
            setError('Favor De Calificar');
            return;
        }
        
          const sanitizedComment = DOMPurify.sanitize(comment.trim());
          const sanitizedMain = DOMPurify.sanitize(main.trim());

          if(sanitizedComment === '' || sanitizedMain === ''){
            setError('Favor de Modificar. Se Detectaron Caracteres Peligrosos (<>{}())');
            return;
          };

            /* DATA */
            const formData = new FormData();
            formData.append('market', data.name);
            formData.append('main', sanitizedMain);
            formData.append('comments', sanitizedComment);
            formData.append('rating', rating);

        dispatch(sendUserReview(formData));

        setNewReview(false);
      };

    /* GET PAGE INFO */
    const {name} = useParams();
    let data;
    const response = whichMarket(name);
    if(response === "No Market Info"){
        navigate('/notfound');
    } else {
        data = response;
    };

    return(
        <section id='reviewPage'>
                    {newReview && (
                        <div id='reviewContent'>
                            <Link to={`/menu/${name}`} className='nav'> ➤ Ir al Menu</Link>
                            <h1>{data.name}</h1>
                            <a href={data.href} target='_blank' rel='noreferrer' className='nav'>{data.location}</a>
                            <p className='end'>Horario: {data.open}</p>

                            <div id='formReview'>
                                <input type='text' value={main} required onChange={(e) => setMain(e.target.value)} placeholder='Asunto'/>
                                <textarea value={comment} required onChange={(e) => setComment(e.target.value)} placeholder='Comentarios'/>

                                <div className='center'>
                                <div className="rating">
                                    <input type="radio" id="star5" name="rating" onClick={()=>{setRating("5")}}/>
                                    <label for="star5"></label>

                                    <input type="radio" id="star4" name="rating" onClick={()=>{setRating("4")}}/>
                                    <label for="star4"></label>

                                    <input type="radio" id="star3" name="rating" onClick={()=>{setRating("3")}}/>
                                    <label for="star3"></label>

                                    <input type="radio" id="star2" name="rating" onClick={()=>{setRating("2")}}/>
                                    <label for="star2"></label>
                                    
                                    <input type="radio" id="star1" name="rating" onClick={()=>{setRating("1")}}/>
                                    <label for="star1"></label>
                                </div>
                                </div>            

                                {error && <div className='err'>{error}</div>}
                                <div id='homeNav'>
                                    <button onClick={handleSubmit} className='homeBtn'>Subir Reseña</button>
                                </div>

                            </div>
                        </div>
                    )}

                    {loading && (
                        <div class="loader"></div>
                    )}

                    {((status === 'COMPLETED' && !loading) && !newReview) && (<Navigate to='/action/status'/>)}
                    {((status === 'ERROR' && !loading) && !newReview) && (<Navigate to='/action/status'/>)}
        </section>
    );
}