import {React} from 'react';
import {Link} from 'react-router-dom';
import embajadaLogo from '../../assets/Logo_embajada.png';
import './homePage.css';

export default function HomePage({setNewReview}){
    function reset(){
        setNewReview(true);
    };

    return(
        <section id='homePage'>
            <div id='arcoHome'>
                <img src={embajadaLogo} alt='Embajada'></img>
            </div>
            
            <div id='hello'>
                <h3>Bienvenido a SAVI</h3>
                <h2>Sistema Anti Violencia Intramarital</h2>
            </div>

            <nav id='homeNav'>
                <Link to='/report' className='homeBtn' onClick={reset}>Hacer un reporte</Link>
                <Link to='/informacion' className='homeBtn' onClick={reset} style={{'marginBottom':'5%'}}>Información</Link>
            </nav>

        </section>
    );    
}