import React from 'react';
import {useSelector} from 'react-redux';
import {Link} from 'react-router-dom';
import {reviewStatusSelector, reviewErrorSelector, reviewMsgSelector} from '../../app/slices/reviewsSlice';
import './statusReview.css';

export default function StatusReview(){
    const err = useSelector(reviewErrorSelector);
    const status = useSelector(reviewStatusSelector);
    const msg = useSelector(reviewMsgSelector);
    return(
        <section id='statusReviewPage'>
            {/* SUCCESS */}
            {status === "COMPLETED" && (
                <div id='success'>
                    <i class='fas fa-check'></i>
                    <h1>EXITO</h1>
                    <p className='msg'>{msg}</p>
                    <p>¡Muchas Gracias!</p>
                    <Link to='/home' style={{'textDecoration': 'underline'}}>Agregar Nueva Reseña</Link>
                </div>
            )}

            {/* ERROR */}
            {status === "ERROR" && (
                <div id='error'>
                    <i class='fas fa-exclamation-triangle'></i>
                    <h1>ERROR</h1>
                    <p className='msg'>{err}</p>
                    <p>Intentelo Más Tarde<br/> Disculpe Las Molestias</p>
                    <Link to='/home' style={{'textDecoration': 'underline'}}>Ir A Pagina De Inicio</Link>
                </div>
            )}

        </section>
    );
}