import React, {useEffect, useState} from 'react';
import {useSelector} from 'react-redux';
import { RouterProvider, createBrowserRouter, createRoutesFromElements, Route, Navigate, useLocation} from 'react-router-dom';
import AppLayout from './AppLayout';
import HomePage from '../pages/home/HomePage';
import ReviewPage from '../pages/review/ReviewPage';
import ReportPage from '../pages/report/Report';
import Videos from '../pages/videos/Videos';
import NotFound from '../pages/errors/NotFoundPage';
import StatusReview from '../pages/review/StatusReview';

import {reviewStatusSelector} from './slices/reviewsSlice';

/* SCROLL */
function ScrollToTop() {
  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location.pathname]);

  return null;
}


function App() {
  /* ROUTER */
  /* GLOBAL STATES */
  const [newReview, setNewReview] = useState(true);
  const status = useSelector(reviewStatusSelector);
  const router = createBrowserRouter(createRoutesFromElements(
    <>

    <Route path='/' element={
      <>
      <ScrollToTop />
      <AppLayout/>
      </>
    }>
      <Route index element={ <Navigate to="/home"/> }/>
      <Route exact path="home" element={<HomePage setNewReview={setNewReview}/>}/>
      <Route exact path="informacion" element={<Videos/>}/>
      <Route exact path="review/:name" element={<ReviewPage newReview={newReview} setNewReview={setNewReview}/>}/>
      <Route exact path="report" element={<ReportPage newReview={newReview} setNewReview={setNewReview}/>}/>
      <Route exact path="action/status" element={status==='none'? <Navigate to="/home"/>:<StatusReview/>}/>

      {/* FAIL */}
      <Route path='*' element={<NotFound/>}/>
      
    </Route>

     </>
  )
  );
  
  return (
    <RouterProvider router={router} basename='/*'/>
  );
}

export default App;