import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, Link, Navigate } from 'react-router-dom';
import DOMPurify from 'dompurify';
import { reviewLoaderSelector, reviewStatusSelector, sendUserReview } from '../../app/slices/reviewsSlice';
import '../report/report.css';

export default function Report({ newReview, setNewReview }) {
  const dispatch = useDispatch();
  const loading = useSelector(reviewLoaderSelector);
  const status = useSelector(reviewStatusSelector);

  const [image, setImage] = useState(null); // Solo una imagen
  const [error, setError] = useState('');

  // Inicialización de estados
  const [level, setLevel] = useState(null);
  const [place, setPlace] = useState('');
  const [name, setName] = useState('');
  const [age, setAge] = useState('');
  const [contact, setContact] = useState('');
  const [residence, setResidence] = useState('');
  const [comments, setComments] = useState('');

  const handleImageChange = (e) => {
    const file = e.target.files[0]; // Solo seleccionamos el primer archivo
    setImage(file);
  };

  const handleSubmit = () => {
    // Validaciones por campo
    if (!name) {
      setError('Favor de ingresar el nombre.');
      return;
    }

    if (!age) {
      setError('Favor de ingresar la edad.');
      return;
    }

    if (!contact) {
      setError('Favor de ingresar un medio de contacto.');
      return;
    }

    if (!residence) {
      setError('Favor de ingresar el lugar de residencia.');
      return;
    }

    if (!level) {
      setError('Favor de seleccionar el tipo de violencia a reportar.');
      return;
    }

    if (!place) {
      setError('Favor de proporcionar el lugar del incidente.');
      return;
    }
    if (!comments) {
      setError('Favor de proporcionar el lugar del incidente.');
      return;
    }
    if (!image) {
      setError('Favor de subir 1 foto.');
      return;
    }

    // Manejo de sanitización de texto

    const comments1 = comments;

    const sanitizedComentario = DOMPurify.sanitize(comments1.trim());

    if (sanitizedComentario === '') {
      setError('Favor de modificar. Se detectaron caracteres peligrosos (<>{}())');
      return;
    }

    const respuestaFinal1 = {
      name: name,
      age: age,
      contact: contact,
      residence: residence,
      level: level,
      place: place,
      comments: sanitizedComentario
    };    
    

    // Preparación de FormData
    //const formData = new FormData();
    //formData.append('object', respuestaFinal1 )
    //formData.append('img-file', image);
  

    dispatch(sendUserReview(respuestaFinal1));
    setNewReview(false);
  };
  
  return (
    <section id="reviewPage">
      {newReview && (
        <div id="reviewContent">
          <Link to="/home" className="nav"> ➤ Ir al Menu</Link>
          <h1>Reportes</h1>
          <a
            href="https://embamex.sre.gob.mx/marruecos/index.php/es/"
            target="_blank"
            rel="noreferrer"
            className="nav"
          >
            Visita nuestra página web oficial
          </a>
          <div id="formReview">
            {/* Inputs */}
            <label htmlFor="name">Nombre</label>
            <input
              id="name"
              type="text"
              value={name}
              required
              onChange={(e) => setName(e.target.value)}
              placeholder="Nombre"
            />

            <label htmlFor="age">Edad</label>
            <input
              id="age"
              type="number"
              value={age}
              required
              onChange={(e) => setAge(e.target.value)}
              placeholder="Edad"
            />

            <label htmlFor="selectLevel">¿Qué tipo de violencia quieres reportar?</label>
            <select
              onChange={(e) => setLevel(e.currentTarget.value)}
              type="number"
              required
              defaultValue="default"
              id="selectLevel"
            >
                <option value="default" disabled hidden>Tipo de violencia</option>
                <option value="1">Bromas hiriente</option>
                <option value="2">Chantajear</option>
                <option value="3">Mentir, engañar</option>
                <option value="4">Ignorar, ley del hielo</option>
                <option value="5">Celar</option>
                <option value="6">Acechar/Stalkear redes sociales</option>
                <option value="7">Culpabilizar</option>
                <option value="8">Descalificar</option>
                <option value="9">Ridiculizar, Ofender</option>
                <option value="10">Humillar en público</option>
                <option value="11">Intimidar, Amenazar</option>
                <option value="12">Controlar, Prohibir</option>
                <option value="13">Destruir artículos personales</option>
                <option value="14">Manosear</option>
                <option value="15">Caricias agresivas</option>
                <option value="16">Golpear "jugando"</option>
                <option value="17">Pellizcar, Arañar</option>
                <option value="18">Empujar, Jalonear</option>
                <option value="19">Cachetear</option>
                <option value="20">Patear</option>
                <option value="21">Encerrar, Aislar</option>
                <option value="22">Sextorsión</option>
                <option value="23">Amenzar con objetos o armas</option>
                <option value="24">Difundir contenido intimo no consensuado</option>
                <option value="25">Amenazar de muerte</option>
                <option value="26">Forzar a una relación sexual</option>
                <option value="27">Abuso sexual</option>
                <option value="28">Violar</option>
                <option value="29">Mutilar</option>
                <option value="30">Homicidio/Feminicidio</option>                
            </select>


          {/* INPUTS */}
          <label htmlFor="place">¿Dónde sucede/sucedio la violencia?</label>
            <input
              id="place"
              type="text"
              value={place}
              required
              onChange={(e) => setPlace(e.target.value)}
              placeholder="Lugar"
            />

            <label htmlFor="residence">¿Dónde reside actualmente?</label>
            <input
              id="residence"
              type="text"
              value={residence}
              required
              onChange={(e) => setResidence(e.target.value)}
              placeholder="Residencia"
            />

            <label htmlFor="contact">Medio de contacto</label>
            <input
              id="contact"
              type="text"
              value={contact}
              required
              onChange={(e) => setContact(e.target.value)}
              placeholder="Contacto"
            />

            
          <label htmlFor="comments">Comentarios</label>
            <input
              id="comments"
              type="text"
              value={comments}
              required
              onChange={(e) => setComments(e.target.value)}
              placeholder="Comentarios"
            />

            {/* Imagen */}
            <label htmlFor="file-input">Evidencia</label>
            <div className="image-uploader end">
              <label htmlFor="file-input" className="custom-file-upload">
                <i className="fas fa-cloud-upload-alt"></i> Cargar Imagen
              </label>
              <input
                id="file-input"
                type="file"
                accept="image/*"
                onChange={handleImageChange}
                required
                className="hide"
              />
              {image && <img src={URL.createObjectURL(image)} alt="Imagen seleccionada" />}
            </div>

            {error && <div className="err">{error}</div>}
            <div id="homeNav">
              <button onClick={handleSubmit} className="homeBtn">Subir Reporte</button>
            </div>
          </div>
        </div>
      )}

      {loading && <div className="loader"></div>}

      {((status === 'COMPLETED' && !loading) && !newReview) && (<Navigate to="/action/status" />)}
      {((status === 'ERROR' && !loading) && !newReview) && (<Navigate to="/action/status" />)}
    </section>
  );
}