import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { sendReview } from '../../apis/reviewsApi';

/* FUNCTIONS */
export const sendUserReview = createAsyncThunk(
  'reviews/sendRequest',
  async (data) => {
    try {
      const response = await sendReview(data);
      return response;
    } catch(err) {
      throw err;
    }
  }
);

/* SLICE */
const initialState = {
    msg: '',
    status: 'none',
    loading: false,
    error: false,
  }
  const reviewSlice = createSlice({
    name: 'reviews',
    initialState,
    reducers: {},
    extraReducers: builder => {
      builder
        /* all products */
      .addCase(sendUserReview.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(sendUserReview.fulfilled, (state, action) => {
        state.loading = false;
        state.status = 'COMPLETED';
        state.msg = action.payload;
      })
      .addCase(sendUserReview.rejected, (state, action) => {
        state.loading = false;
        state.status = 'ERROR';
        state.error = action.payload === undefined? 'Error De Conexión Del Servidor':action.payload;
      })
    }
  });
  
  // Export reducer
  export const reviewMsgSelector = (state) => state.reviews.msg;
  export const reviewStatusSelector = (state) => state.reviews.status;
  export const reviewLoaderSelector = (state) => state.reviews.loading;
  export const reviewErrorSelector = (state) => state.reviews.error;
  export default reviewSlice.reducer;